export async function fetchAniListExplore(
  page: number = 1,
  search: string = '',
  type: 'ANIME' | 'MANGA' = 'ANIME',
  sort: string = 'POPULARITY_DESC'
) {
  const query = `
    query ($page: Int, $search: String, $type: MediaType, $sort: [MediaSort]) {
      Page(page: $page, perPage: 20) {
        media(search: $search, type: $type, sort: $sort) {
          id
          title {
            romaji
            english
          }
          coverImage {
            medium
          }
          format
          type
          genres
          seasonYear
        }
      }
    }
  `;

  const variables = {
    page,
    search: search || null,
    type,
    sort: [sort] // this must be wrapped in an array
  };

  try {
    const response = await fetch('https://graphql.anilist.co', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ query, variables })
    });

    const json = await response.json();

    if (!json?.data?.Page?.media) {
      console.error('AniList API returned incomplete data:', json);
      return [];
    }

    return json.data.Page.media;
  } catch (error) {
    console.error('AniList fetch error:', error);
    return [];
  }
}